import { GetDeviceLiveFunction } from '@/api/add';
export default {
  name: 'BoardCold',
  data: function data() {
    return {
      cold1: '',
      cold2: '',
      cold3: ''
    };
  },
  created: function created() {
    var _this = this;

    GetDeviceLiveFunction({
      deviceId: this.$route.query.id
    }).then(function (res) {
      if (!res.data) {
        _this.cold1 = '';
        _this.cold2 = '';
        _this.cold3 = '';
      } else {
        res.data.functionValue.deviceList.map(function (item) {
          if (item.deviceType === 'Cold1') {
            _this.cold1 = item.playUrl;
          } else if (item.deviceType === 'Cold2') {
            _this.cold2 = item.playUrl;
          } else if (item.deviceType === 'Cold3') {
            _this.cold3 = item.playUrl;
          }
        });
      }
    });
  }
};