import { GetStatusById } from '@/api/equipmentManagement/status';
import echarts from 'echarts';
import { setInterval, clearInterval } from 'timers';
export default {
  data: function data() {
    return {
      id: null,
      one: parseInt('00000000', 2),
      two: parseInt('00010000', 2),
      three: parseInt('00100000', 2),
      four: parseInt('00110000', 2),
      allTemp: parseInt('11110000', 2),
      value: [],
      myChart: null,
      timer: null,
      floatNumberAcc: null
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.id = this.$route.query.id;
    var dom = this.$refs.echarts;
    this.myChart = echarts.init(dom);
    this.getData();
    this.timer = setInterval(function () {
      _this.getData();
    }, 10000);
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    getData: function getData() {
      var _this2 = this;

      GetStatusById(this.id).then(function (res) {
        _this2.Acc_Sample_OutMode = res.data.statusData['Acc_Sample_OutMode']; // 计算样点数

        if ((_this2.Acc_Sample_OutMode & _this2.allTemp) === _this2.one) {
          _this2.floatNumberAcc = 256;
        } else if ((_this2.Acc_Sample_OutMode & _this2.allTemp) === _this2.two) {
          _this2.floatNumberAcc = 512;
        } else if ((_this2.Acc_Sample_OutMode & _this2.allTemp) === _this2.three) {
          _this2.floatNumberAcc = 1024;
        } else if ((_this2.Acc_Sample_OutMode & _this2.allTemp) === _this2.four) {
          _this2.floatNumberAcc = 4096;
        }

        _this2.Acc_Rate = res.data.statusData['Acc_Rate'];
        var value = (res.data.pointData['Acc_Amp'] || []).map(function (item) {
          if (item.key * (_this2.Acc_Rate / _this2.floatNumberAcc) === 0) {
            return [Math.floor(item.key * (_this2.Acc_Rate / _this2.floatNumberAcc)), (item.value / _this2.floatNumberAcc / 4095 * 3.3 * 1000 / 26.4).toFixed(3)];
          } else {
            return [Math.floor(item.key * (_this2.Acc_Rate / _this2.floatNumberAcc)), (item.value / (_this2.floatNumberAcc / 2) / 4095 * 3.3 * 1000 / 26.4).toFixed(3)];
          }
        });

        _this2.$set(_this2, 'value', value);

        _this2.init();
      });
    },
    init: function init() {
      var option = {
        tooltip: {
          trigger: 'axis',
          formatter: function formatter(params) {
            return "".concat(params[0].value[0], "<br />\u632F\u52A8\u52A0\u901F\u5EA6:").concat(params[0].value[1]);
          }
        },
        title: {
          text: '振动加速度图',
          textStyle: {
            color: '#fff',
            fontSize: 26
          },
          subtextStyle: {
            color: '#fff',
            fontSize: 14
          },
          left: 'center',
          top: '2%'
        },
        xAxis: {
          type: 'value',
          splitLine: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          },
          axisTick: {
            show: true
          },
          axisLabel: {
            color: '#fff',
            fontSize: 14
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            show: false,
            lineStyle: {
              color: '#fff'
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          },
          axisTick: {
            show: true
          },
          axisLabel: {
            color: '#fff',
            fontSize: 14
          }
        },
        series: [{
          type: 'bar',
          animation: false,
          barWidth: 4,
          data: this.value,
          tooltip: {
            show: false
          },
          itemStyle: {
            color: '#ff8b5d'
          }
        }, {
          type: 'scatter',
          data: this.value,
          symbolSize: 8,
          itemStyle: {
            borderWidth: 0,
            opacity: 1,
            color: '#ff8b5d'
          }
        }]
      };
      this.myChart.setOption(option);
    }
  }
};