import { getDeviceData } from '@/api/devicelist';
import echarts from 'echarts';
export default {
  props: ['data'],
  data: function data() {
    return {
      charts: {
        unit: '℃',
        names: ['当前温度', '上限温度', '下限温度'],
        lineX: [],
        value: [[], [], []]
      },
      myChart: null,
      timer: null,
      cmd: null,
      currentTemperature: undefined,
      topTemperature: undefined,
      lowerTemperature: undefined
    };
  },
  created: function created() {
    this.getTitleData();
  },
  watch: {
    data: function data() {
      this.getTitleData();
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.timer = setInterval(function () {
      _this.getData();
    }, 10000);
    this.getData();
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    getTitleData: function getTitleData() {
      var arr = this.data.reduce(function (pre, next) {
        return pre.concat(next.fields);
      }, []);
      var n = 1;
      var count = 0;

      while (typeof arr.find(function (item) {
        return item.fieldCode == 'Freeze.FreezerController.Temperature.CurrentTemperature' + n;
      }) !== "undefined") {
        count = count + Number(arr.find(function (item) {
          return item.fieldCode == 'Freeze.FreezerController.Temperature.CurrentTemperature' + n;
        }).fieldValue);
        n++;
      }

      if (n !== 1) {
        this.currentTemperature = (count / (n - 1)).toFixed(2);
      }

      this.topTemperature = arr.find(function (item) {
        return item.fieldCode == 'Freeze.FreezerController.Temperature.TopTemperature';
      }).fieldValue;
      this.lowerTemperature = arr.find(function (item) {
        return item.fieldCode == 'Freeze.FreezerController.Temperature.LowerTemperature';
      }).fieldValue;
    },
    getData: function getData() {
      var _this2 = this;

      getDeviceData({
        cmd: 850,
        deviceId: this.$route.query.id,
        startTime: this.$moment().subtract(30, 'minutes').utc().format(),
        pageIndex: 1,
        pageSize: 200,
        fields: 'Freeze.FreezerController.Temperature.CurrentTemperature1,Freeze.FreezerController.Temperature.CurrentTemperature2,Freeze.FreezerController.Temperature.TopTemperature,Freeze.FreezerController.Temperature.LowerTemperature'
      }).then(function (res) {
        var Temperature0 = res.data.items.map(function (item) {
          var n = 1;
          var count = 0;

          while (typeof item.data['Freeze.FreezerController.Temperature.CurrentTemperature' + n] !== 'undefined') {
            count = count + item.data['Freeze.FreezerController.Temperature.CurrentTemperature' + n];
            n++;
          }

          if (n === 1) {
            return '--';
          }

          return count / (n - 1);
        });
        var Temperature1 = res.data.items.map(function (item) {
          return item.data['Freeze.FreezerController.Temperature.TopTemperature'];
        });
        var Temperature2 = res.data.items.map(function (item) {
          return item.data['Freeze.FreezerController.Temperature.LowerTemperature'];
        });
        var X = res.data.items.map(function (item) {
          return _this2.$moment.utc(item.time).local().format('YYYY-MM-DD HH:mm:ss');
        });

        _this2.$set(_this2.charts.value, '0', Temperature0.reverse());

        _this2.$set(_this2.charts.value, '1', Temperature1.reverse());

        _this2.$set(_this2.charts.value, '2', Temperature2.reverse());

        _this2.$set(_this2.charts, 'lineX', X.reverse());

        _this2.init();
      });
    },
    init: function init() {
      var color = ['#17FFF3', '#FF6461', '#4FAD32', '#A52A2A', '#FFFFC8', '#FFFF82'];
      var lineY = [];

      for (var i = 0; i < this.charts.names.length; i++) {
        var x = i;

        if (x > color.length - 1) {
          x = color.length - 1;
        }

        var data = {
          name: this.charts.names[i],
          type: 'line',
          color: color[x],
          smooth: true,
          symbol: 'circle',
          symbolSize: 5,
          data: this.charts.value[i]
        };
        lineY.push(data);
      }

      lineY[0].markLine = {
        silent: true,
        data: [{
          yAxis: 5
        }, {
          yAxis: 100
        }, {
          yAxis: 200
        }, {
          yAxis: 300
        }, {
          yAxis: 400
        }]
      };
      var option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: this.charts.names,
          textStyle: {
            fontSize: 22,
            color: '#00FDFF'
          },
          right: '4%'
        },
        grid: {
          top: '14%',
          left: '4%',
          right: '4%',
          bottom: '12%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.charts.lineX,
          axisLabel: {
            textStyle: {
              color: '#00FDFF'
            },
            formatter: function formatter(params) {
              return params.split(' ')[0] + '\n' + params.split(' ')[1];
            }
          }
        },
        yAxis: {
          name: this.charts.unit,
          type: 'value',
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              color: '#00FDFF'
            }
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#00FDFF'
            }
          }
        },
        series: lineY
      };

      if (!this.myChart) {
        this.myChart = echarts.init(this.$refs.echarts);
      }

      this.myChart.setOption(option);
    }
  }
};