import request from '@/utils/request';
var api = {
  Get: '/api/dms/DeviceFunction/Get',
  GetByDeviceId: '/api/dms/DeviceFunction/GetByDeviceId',
  GetFunctionType: '/api/dms/DeviceFunction/GetFunctionType',
  Create: '/api/dms/DeviceFunction/Create',
  Update: '/api/dms/DeviceFunction/Update',
  Delete: '/api/dms/DeviceFunction/Delete',
  GetDeviceLiveFunction: '/api/dms/DeviceFunction/GetDeviceLiveFunction'
}; // 根据Code修改业务参数

export function Get(obj) {
  return request({
    url: api.Get,
    method: 'get',
    data: obj
  });
} // 根据设备ID获取功能列表

export function GetByDeviceId(id) {
  return request({
    url: api.GetByDeviceId,
    method: 'get',
    params: id
  });
} // 获取设备附加功能类型列表

export function GetFunctionType() {
  return request({
    url: api.GetFunctionType,
    method: 'get'
  });
} // 创建设备附加功能

export function Create(obj) {
  return request({
    url: api.Create,
    method: 'post',
    data: obj
  });
} // 更新设备附加功能

export function Update(obj) {
  return request({
    url: api.Update,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 删除设备附加功能

export function Delete(id) {
  return request({
    url: api.Delete,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: id
  });
} // 根据设备ID获取设备直播附加功能 

export function GetDeviceLiveFunction(id) {
  return request({
    url: api.GetDeviceLiveFunction,
    method: 'get',
    params: id
  });
}