import echarts from 'echarts';
import { withBorder, resize } from '@/mixin';
export default {
  name: 'BoardCircleStatistics',
  mixins: [withBorder, resize],
  props: {
    title: {
      default: "在线率"
    },
    titleColor: {
      default: "#fff"
    },
    backgroundColor: {// default: "#0f2d55"
    },
    seriesName: {
      default: "在线率123"
    },
    seriesColor: {
      default: "#01b0d1"
    },
    dataValue1: {
      default: "20"
    },
    dataValue2: {
      default: "80"
    },
    dataName1: {
      default: "在线率"
    },
    textColor: {
      default: "#fff"
    }
  },
  computed: {
    allProps: function allProps() {
      return {
        title: this.title,
        titleColor: this.titleColor,
        backgroundColor: this.backgroundColor,
        seriesName: this.seriesName,
        seriesColor: this.seriesColor,
        dataValue1: this.dataValue1,
        dataValue2: this.dataValue2,
        dataName1: this.dataName1,
        textColor: this.textColor
      };
    }
  },
  watch: {
    allProps: function allProps() {
      this.setEchart();
    }
  },
  mounted: function mounted() {
    this.setEchart();
  },
  methods: {
    setEchart: function setEchart() {
      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: function formatter(params) {
            var res = params.seriesName;
            res += '<br/>' + params.name + ' : ' + params.percent + '%';
            return res;
          }
        },
        backgroundColor: this.backgroundColor,
        title: {
          text: this.title,
          x: 'center',
          y: 'top',
          textAlign: 'left',
          textStyle: {
            color: this.titleColor
          }
        },
        series: [{
          name: this.seriesName,
          itemStyle: {
            normal: {
              color: this.seriesColor
            }
          },
          center: ['50%', '55%'],
          radius: ['80%', '70%'],
          type: 'pie',
          labelLine: {
            normal: {
              show: false
            }
          },
          data: [{
            value: this.dataValue1,
            name: this.dataName1,
            label: {
              normal: {
                formatter: '{d} %',
                position: 'center',
                show: true,
                textStyle: {
                  fontSize: '20',
                  fontWeight: 'bold',
                  color: this.textColor
                }
              }
            }
          }, {
            value: this.dataValue2,
            tooltip: {
              show: false
            },
            itemStyle: {
              normal: {
                color: '#465262'
              },
              emphasis: {
                color: '#0e1c3d'
              }
            },
            hoverAnimation: false
          }]
        }]
      };
      this.myChart.setOption(option);
    }
  }
};