import { withBorder, resize } from '@/mixin';
import echarts from 'echarts';
export default {
  name: 'OpenRate',
  mixins: [withBorder, resize],
  props: {
    title: {
      default: '开机率统计情况'
    },
    xData: {
      default: function _default() {
        return ['星沙水厂', '保定泵站', '长河泵站', '武汉泵站', '南京泵站', '北京泵站', '天津泵站', '大连泵站', '西安泵站'];
      }
    },
    views: {
      default: function _default() {
        return ['正在运行', '未开机'];
      }
    },
    viewColors: {
      default: function _default() {
        return ['#3893e5', '#626262'];
      }
    },
    Ltitle: {
      default: '开机率'
    },
    Ldata: {
      default: function _default() {
        return [0, 74, 98, 48, 48, 74, 98, 48, 100];
      }
    },
    data: {
      default: function _default() {
        return [[98, 68, 100, 40, 30, 68, 100, 40, 20], [100, 100, 100, 100, 100, 100, 100, 100, 100]];
      }
    }
  },
  computed: {
    allProps: function allProps() {
      return {
        title: this.title,
        views: this.views,
        Ltitle: this.Ltitle,
        Ldata: this.Ldata,
        viewColors: this.viewColors,
        xData: this.xData,
        data: this.data
      };
    }
  },
  watch: {
    allProps: function allProps() {
      this.setEchart();
    }
  },
  mounted: function mounted() {
    this.setEchart();
  },
  methods: {
    setEchart: function setEchart() {
      var _this = this;

      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        grid: {
          y2: '30',
          x: '50',
          y: '100',
          x2: '15'
        },
        legend: {
          x: '25',
          y: '50',
          itemWidth: 15,
          itemHeight: '10',
          data: this.views.map(function (item) {
            return {
              name: item,
              textStyle: {
                fontWeight: 'bold',
                color: '#e8e8dc',
                fontSize: '10'
              }
            };
          }).concat({
            name: this.Ltitle,
            textStyle: {
              fontWeight: 'bold',
              color: '#0eddfd',
              fontSize: '10'
            }
          })
        },
        title: {
          text: this.title,
          left: 'center',
          top: 20,
          textStyle: {
            color: '#d9efff'
          }
        },
        xAxis: [{
          type: 'category',
          data: this.xData,
          axisLabel: {
            show: true,
            interval: 0,
            textStyle: {
              color: '#d2d4da'
            }
          }
        }],
        yAxis: [{
          type: 'value',
          // interval: 50,
          scale: true,
          splitLine: {
            lineStyle: {
              color: '#1f3054'
            }
          },
          axisLabel: {
            textStyle: {
              color: '#d2d4da',
              fontSize: '9'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#1f3054',
              // 颜色
              width: 1 // 粗细

            }
          }
        }, {
          type: 'value',
          show: false,
          interval: 1,
          axisLabel: {
            show: true,
            interval: 'auto',
            formatter: '{value} %'
          }
        }],
        series: this.views.map(function (item, index) {
          return {
            name: item,
            barWidth: 10,
            borderWidth: '0',
            type: 'bar',
            stack: '总量',
            itemStyle: {
              normal: {
                color: _this.viewColors[index]
              }
            },
            label: {
              normal: {
                color: '#fff',
                show: true,
                position: 'inside',
                zlevel: '1000'
              }
            },
            data: _this.data[index]
          };
        }).concat({
          name: this.Ltitle,
          symbol: 'circle',
          symbolSize: 8,
          smooth: true,
          type: 'line',
          yAxisIndex: 1,
          data: this.Ldata,
          itemStyle: {
            normal: {
              color: '#0eddfd',
              label: {
                show: true,
                position: 'top',
                formatter: '{c}%',
                borderWidth: 10,
                color: '#0eddfd',
                padding: 4
              },
              labelLine: {
                show: false
              }
            }
          }
        })
      };
      this.myChart.setOption(option);
    }
  }
};