import echarts from 'echarts';
export default {
  name: 'Spectrum',
  props: ['data', 'prefix'],
  data: function data() {
    return {
      Acc_Rate: 0,
      // 采样频率,
      Audio_Rate: 0,
      Audio_Sample_OutMode: 0,
      // 采样点
      Acc_Sample_OutMode: 0,
      one: 0,
      two: 0,
      three: 0,
      four: 0,
      floatNumber: 0,
      floatNumberAcc: 0,
      audioAmp: [],
      x: [],
      y: [],
      optionAcc: {},
      optionAudio: {},
      zd: 0,
      valueX: [],
      valueY: [],
      lastArr: [],
      accAmp: [],
      accX: [],
      accY: [],
      lastAcc: [],
      lastAccX: [],
      lastAccY: [],
      max: 0,
      ShakeTemp: 0,
      ShakeBattery: 0
    };
  },
  watch: {
    data: function data() {
      this.x = [];
      this.y = [];
      this.valueX = [];
      this.valueY = [];
      this.accX = [];
      this.accY = [];
      this.audioAmp = [];
      this.accAmp = [];
      this.lastAccX = [];
      this.lastAccY = [];
      this.achieveState(this.prefix);
    }
  },
  mounted: function mounted() {
    this.achieveState(this.prefix);
  },
  methods: {
    achieveState: function achieveState(prefix) {
      var _this = this;

      if (!prefix) {
        prefix = '';
      }

      if (JSON.stringify(this.data.statusData) === '{}' || undefined) return; // 四个状态展示(图顶部)

      if (this.data.statusData.ShakeTemp) {
        this.ShakeTemp = this.data.statusData.ShakeTemp;
      } else {
        this.data.statusData.ShakeTemp = ' --';
      }

      if (this.data.statusData.ShakeBattery) {
        this.ShakeBattery = this.data.statusData.ShakeBattery;
      } else {
        this.data.statusData.ShakeBattery = ' --';
      } // 获取数据


      new Promise(function (resolve) {
        // 声音频谱
        _this.one = parseInt('00000000', 2); // 音频256个采样点

        _this.two = parseInt('00010000', 2); // 音频512个采样点

        _this.three = parseInt('00100000', 2); // 音频1024个采样点

        _this.four = parseInt('00110000', 2); // 音频4096个采样点

        _this.allTemp = parseInt('11110000', 2);
        _this.Audio_Rate = _this.data.statusData[prefix + 'Audio_Rate']; // 采样频率

        _this.Audio_Sample_OutMode = _this.data.statusData[prefix + 'Audio_Sample_OutMode']; // 采样点-输出模式
        // 计算样点数

        if ((_this.Audio_Sample_OutMode & _this.allTemp) === _this.one) {
          _this.floatNumber = 256;
        } else if ((_this.Audio_Sample_OutMode & _this.allTemp) === _this.two) {
          _this.floatNumber = 512;
        } else if ((_this.Audio_Sample_OutMode & _this.allTemp) === _this.three) {
          _this.floatNumber = 1024;
        } else if ((_this.Audio_Sample_OutMode & _this.allTemp) === _this.four) {
          _this.floatNumber = 4096;
        }

        if (!_this.data.pointData[prefix + 'Audio_Amp']) return;

        _this.data.pointData[prefix + 'Audio_Amp'].map(function (item) {
          _this.audioAmp.push({
            key: item.key * (_this.Audio_Rate / _this.floatNumber),
            value: item.value
          });
        });

        _this.audioAmp.map(function (item) {
          _this.valueX.push(item.key);

          _this.valueY.push(item.value);
        }); // 取x轴中最大值


        _this.max = 0;

        function getMaximin(arr, maximin) {
          // eslint-disable-next-line eqeqeq
          if (maximin == 'max') {
            return Math.max.apply(Math, arr); // eslint-disable-next-line eqeqeq
          } else if (maximin == 'min') {
            return Math.min.apply(Math, arr);
          }
        }

        _this.max = getMaximin(_this.valueX, 'max');
        if (_this.max === -Infinity) return; // 没有最大值, 最大值为无穷时

        var c = Array.from(Array(parseInt(_this.max)), function (item, index) {
          return {
            key: index,
            value: 0
          };
        });

        _this.audioAmp.map(function (item) {
          // 替换数组中已经有的项
          c.map(function (i) {
            if (item.key === i.key) {
              i.value = item.value;
            }
          });
        });

        var a = c.concat(_this.audioAmp);
        var result = []; // 去掉重复的对象

        var obj = {};

        for (var i = 0; i < a.length; i++) {
          if (!obj[a[i].key]) {
            result.push(a[i]);
            obj[a[i].key] = true;
          }
        }

        _this.lastArr = result.sort(function (a, b) {
          return a.key - b.key;
        });

        _this.lastArr.map(function (item) {
          _this.x.push(Math.floor(item.key));

          _this.y.push(item.value);
        }); // 振动加速度


        _this.Acc_Sample_OutMode = _this.data.statusData[prefix + 'Acc_Sample_OutMode']; // 采样点-输出模式
        // 计算样点数

        if ((_this.Acc_Sample_OutMode & _this.allTemp) === _this.one) {
          _this.floatNumberAcc = 256;
        } else if ((_this.Acc_Sample_OutMode & _this.allTemp) === _this.two) {
          _this.floatNumberAcc = 512;
        } else if ((_this.Acc_Sample_OutMode & _this.allTemp) === _this.three) {
          _this.floatNumberAcc = 1024;
        } else if ((_this.Acc_Sample_OutMode & _this.allTemp) === _this.four) {
          _this.floatNumberAcc = 4096;
        } // y轴: 非 直流分量(Y 轴) (频率序号 != 0) 的公式为 浮点数幅值<value>/(采样点数/2)/4095*3.3*1000/26.4
        //      直流分量 (Y 轴) (频率序号 == 0) 的公式为 浮点数幅值/(采样点数)/4095*3.3*1000/26.4
        // x = 0 ! / 2
        // 频率(x 轴)计算方式为 频率序号 * (采样频率/采样点数)


        _this.Acc_Rate = _this.data.statusData[prefix + 'Acc_Rate'];

        _this.data.pointData[prefix + 'Acc_Amp'].map(function (item) {
          if (item.key * (_this.Acc_Rate / _this.floatNumberAcc) === 0) {
            _this.accAmp.push({
              key: item.key * (_this.Acc_Rate / _this.floatNumberAcc),
              value: item.value / _this.floatNumberAcc / 4095 * 3.3 * 1000 / 26.4
            });
          } else {
            _this.accAmp.push({
              key: item.key * (_this.Acc_Rate / _this.floatNumberAcc),
              value: item.value / (_this.floatNumberAcc / 2) / 4095 * 3.3 * 1000 / 26.4
            });
          }
        });

        _this.accAmp.map(function (item) {
          _this.accX.push(Math.floor(item.key));

          _this.accY.push(item.value.toFixed(3));
        });

        var accMax = getMaximin(_this.accX, 'max');
        var accC = Array.from(Array(parseInt(accMax)), function (item, index) {
          return {
            key: index,
            value: 0
          };
        }); // 替换数组中已有的项

        _this.accAmp.map(function (item) {
          accC.map(function (i) {
            if (item.key === i.key) {
              i.value = item.value;
            }
          });
        });

        var accA = accC.concat(_this.accAmp);
        var accResult = []; // 去掉重复的对象

        var accObj = {}; // eslint-disable-next-line no-redeclare

        for (var i = 0; i < accA.length; i++) {
          if (!accObj[accA[i].key]) {
            accResult.push(accA[i]);
            accObj[accA[i].key] = true;
          }
        }

        _this.lastAcc = accResult.sort(function (a, b) {
          return a.key - b.key;
        });

        _this.lastAcc.map(function (item) {
          _this.lastAccX.push(parseInt(item.key));

          _this.lastAccY.push(item.value);
        });

        resolve();
      });
      this.echartShow();
      this.accAmpShow();
    },
    echartShow: function echartShow() {
      var container = this.$refs.mainVoice;
      var myChart = echarts.init(container);
      this.optionAcc = {
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        animation: false,
        legend: {
          data: ['声音频谱']
        },
        toolbox: {
          show: true // feature: {
          //   dataView: { show: true, readOnly: false },
          //   saveAsImage: { show: true }
          // }

        },
        calculable: true,
        dataZoom: {
          show: true,
          realtime: true,
          start: 0,
          end: 100
        },
        grid: {
          bottom: '70px'
        },
        xAxis: [{
          type: 'category',
          boundaryGap: true,
          data: this.x,
          axisLabel: {
            // interval: 0,
            rotate: 40
          },
          axisLine: {
            lineStyle: {
              color: '#000',
              // 颜色
              width: 6 // 粗细

            }
          },
          minInterval: 1
        }],
        yAxis: [{
          type: 'value',
          data: this.y,
          axisLabel: {
            formatter: function formatter(value) {
              return value;
            }
          }
        }],
        series: [{
          name: '声音频谱',
          type: 'line',
          showAllSymbol: true,
          // 标注所有数据点,
          symbol: 'circle',
          // 拐点样式
          symbolSize: 4,
          // 拐点大小
          data: this.y,
          itemStyle: {
            normal: {
              borderColor: '#000',
              color: '#000',
              lineStyle: {
                type: 'solid',
                width: 1
              }
            }
          }
        }]
      };
      myChart.setOption(this.optionAcc);
    },
    accAmpShow: function accAmpShow() {
      // 加速度echart
      var container = this.$refs.Aspeed;
      var myChart = echarts.init(container);
      this.optionAudio = {
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        animation: false,
        legend: {
          data: ['振动加速度']
        },
        toolbox: {
          show: true
        },
        calculable: true,
        dataZoom: {
          show: true,
          realtime: true,
          start: 0,
          end: 100
        },
        grid: {
          bottom: '70px'
        },
        xAxis: [{
          type: 'category',
          boundaryGap: true,
          data: this.lastAccX,
          axisLabel: {
            rotate: 40
          },
          axisLine: {
            lineStyle: {
              color: '#000',
              // 颜色
              width: 6 // 粗细

            }
          },
          minInterval: 1
        }],
        yAxis: [{
          type: 'value',
          data: this.lastAccY,
          axisLabel: {
            formatter: function formatter(value) {
              return value.toFixed(3);
            }
          }
        }],
        series: [{
          name: '振动加速度',
          type: 'line',
          showAllSymbol: true,
          // 标注所有数据点,
          symbol: 'circle',
          // 拐点样式
          symbolSize: 4,
          // 拐点大小
          data: this.lastAccY,
          itemStyle: {
            normal: {
              borderColor: '#000',
              lineStyle: {
                type: 'solid',
                width: 1
              },
              color: '#000'
            }
          }
        }]
      }; //   delete this.optionAudio.dataZoom

      myChart.setOption(this.optionAudio);
    }
  }
};