      if (n !== 1) {
        this.currentTemperature = (count / (n - 1)).toFixed(2);
      }

      this.topTemperature = arr.find(function (item) {
        return item.fieldCode == 'Freeze.FreezerController.Temperature.TopTemperature';
      }).fieldValue;
