import echarts from 'echarts';
import { withBorder, resize } from '@/mixin';
export default {
  name: "TotalTraffic",
  mixins: [withBorder, resize],
  props: {
    title: {
      default: '累计流量'
    },
    value: {
      default: 1
    },
    symbol: {
      default: 'm³/h'
    }
  },
  computed: {
    textTitle: function textTitle() {
      return this.title;
    },
    textTip: function textTip() {
      return this.title;
    },
    dataValue: function dataValue() {
      return [{
        value: this.value,
        name: this.symbol
      }];
    },
    allProps: function allProps() {
      return {
        title: this.title,
        value: this.value,
        symbol: this.symbol
      };
    }
  },
  watch: {
    allProps: function allProps() {
      this.setEchart();
    }
  },
  data: function data() {
    return {
      color: [[0.09, 'lime'], [0.82, '#1e90ff'], [1, '#ff4500']]
    };
  },
  mounted: function mounted() {
    this.setEchart();
  },
  methods: {
    setEchart: function setEchart() {
      // 流量
      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        title: {
          text: this.textTitle,
          left: 'center',
          textStyle: {
            // 其余属性默认使用全局文本样式，详见TEXTSTYLE
            fontWeight: 'bolder',
            fontSize: 20,
            fontStyle: 'italic',
            color: '#fff',
            shadowColor: '#fff',
            // 默认透明
            shadowBlur: 10
          }
        },
        tooltip: {
          formatter: '{a} <br/>{c} {b}'
        },
        series: [{
          name: this.textTip,
          type: 'gauge',
          min: 0,
          max: 1500,
          splitNumber: 6,
          radius: '100%',
          axisLine: {
            // 坐标轴线
            lineStyle: {
              // 属性lineStyle控制线条样式
              color: this.color,
              width: 3,
              shadowColor: '#fff',
              // 默认透明
              shadowBlur: 10
            }
          },
          pointer: {
            width: 5,
            // 指针的宽度
            length: '65%' // 指针长度，按照半圆半径的百分比

          },
          splitLine: {
            // 分隔线样式。
            show: false,
            length: 5
          },
          title: {
            textStyle: {
              // 其余属性默认使用全局文本样式，详见TEXTSTYLE
              fontWeight: 'bolder',
              fontSize: 20,
              fontStyle: 'italic',
              color: '#fff',
              shadowColor: '#fff',
              // 默认透明
              shadowBlur: 10
            }
          },
          detail: {
            backgroundColor: 'rgba(30,144,255,0.8)',
            borderWidth: 0.2,
            borderColor: '#fff',
            shadowColor: '#fff',
            // 默认透明
            shadowBlur: 4.5,
            offsetCenter: [0, '85%'],
            // x, y，单位px
            textStyle: {
              // 其余属性默认使用全局文本样式，详见TEXTSTYLE
              fontWeight: 'bolder',
              color: '#fff',
              fontSize: 20
            }
          },
          data: this.dataValue
        }]
      };
      this.myChart.setOption(option);
    }
  }
};