import QRCode from 'qrcodejs2';
export default {
  name: 'QRCode',
  props: ['url'],
  mounted: function mounted() {
    this.qrcode();
  },
  methods: {
    qrcode: function qrcode() {
      new QRCode(this.$refs.qrcode, {
        text: this.url,
        width: 120,
        height: 120
      });
    }
  }
};