import { withBorder } from '@/mixin';
import { getDevice } from '@/api/devicelist';
export default {
  mixins: [withBorder],
  data: function data() {
    return {
      status: {
        0: 'grey',
        1: 'green',
        2: 'red'
      },
      type: '',
      list: [],
      positions: [],
      names: ['容城1号机组', '容城2号机组', '容城3号机组', '容城4号机组', '安新1号机组', '安新2号机组', '安新3号机组', '安新4号机组'],
      positionsList: [],
      text: "",
      timer: null
    };
  },
  created: function created() {
    var props = JSON.parse(this.$store.state.user.config.boardTemplate).find(function (item) {
      return item.code === 'BgImg';
    }).props;
    this.positions = props.positions;
    this.names = props.names;
    this.text = props.text;
    this.type = this.$route.fullPath.includes('home') ? '' : 'white';
  },
  mounted: function mounted() {
    var _this = this;

    this.init();
    this.timer = setInterval(function () {
      _this.init();
    }, 7000);
  },
  methods: {
    init: function init() {
      var _this2 = this;

      getDevice({
        isTop: true,
        pageIndex: 1,
        pageSize: 900000 // 后台说取个很大的值

      }).then(function (res) {
        _this2.$set(_this2, 'list', res.data.items);

        _this2.positionsList = _this2.positions.map(function (item) {
          if (!_this2.list.some(function (cur) {
            return cur.code === item;
          })) {
            return '';
          }

          return item;
        });
      });
    },
    goDetail: function goDetail(index) {
      var _this3 = this;

      if (this.positionsList[index]) {
        this.$emit('mapClick', this.list.find(function (item) {
          return item.code == _this3.positionsList[index];
        }));
      }
    }
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  }
};