import request from '@/utils/request';
var api = {
  getList: '/api/dms/DataView/Get',
  getById: '/api/dms/DataView/GetById',
  updateById: '/api/dms/DataView/Update',
  deleteDataView: '/api/dms/DataView/Delete',
  createDataView: '/api/dms/DataView/Create',
  getTemplateInfoByDeviceId: '/api/dms/DataView/GetViewByDeviceId',
  exportExcel: '/api/dms/DeviceData/ExportExcel'
}; // 获取数据视图列表

export function getList(obj) {
  return request({
    url: api.getList,
    method: 'get',
    params: obj,
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 获取数据视图详情

export function getById(id) {
  return request({
    url: api.getById,
    method: 'get',
    params: {
      id: id
    },
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 更新数据视图配置

export function UpdateById(obj) {
  return request({
    url: api.updateById,
    method: 'post',
    data: obj,
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 删除数据视图

export function deleteDataView(id) {
  return request({
    url: api.deleteDataView,
    method: 'post',
    data: {
      id: id
    },
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 新增数据视图

export function CreateDataView(obj) {
  return request({
    url: api.createDataView,
    method: 'post',
    data: obj,
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 获取数据视图模板信息

export function GetTemplateInfoByDeviceId(deviceId) {
  return request({
    url: api.getTemplateInfoByDeviceId,
    method: 'get',
    params: {
      deviceId: deviceId
    },
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 导出设备数据

export function exportExcel(obj) {
  return request({
    url: api.exportExcel,
    method: 'get',
    params: obj
  });
}