import request from '@/utils/request';
var api = {
  GetStatusById: '/api/dms/Device/GetStatusById',
  GetById: '/api/dms/Device/GetById',
  GetUtcTime: '/api/dms/DeviceData/GetUtcTime'
};
/**
 * @description 设备当前状态
 * @param  {String} id
 */

export function GetStatusById(id) {
  return request({
    url: api.GetStatusById,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      id: id
    }
  });
}
/**
 * @description 获取设备详情
 * @param  {String} id
 */

export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      id: id
    }
  });
} // 返回Utc时间 

export function GetUtcTime() {
  return request({
    url: api.GetUtcTime,
    method: 'get'
  });
}