import request from '@/utils/request';
var api = {
  getAll: '/api/dms/DeviceTag/GetAll',
  getDeviceTagsByTypeId: '/api/dms/DeviceTag/GetDeviceTagsByTypeId',
  updateDeviceTags: '/api/dms/DeviceTag/UpdateDeviceTags',
  createDeviceTags: '/api/dms/DeviceTag/CreateDeviceTags',
  deleteDeviceTagType: '/api/dms/DeviceTag/deleteDeviceTagType',
  GetDeviceTags: '/api/dms/DeviceTag/GetDeviceTags'
}; // 获取全部的设备标签

export function getAll() {
  return request({
    url: api.getAll,
    method: 'get'
  });
} // 根据id获取设备标签

export function getDeviceTagsByTypeId(id) {
  return request({
    url: api.getDeviceTagsByTypeId,
    method: 'get',
    params: {
      id: id
    }
  });
} // 修改设备标签

export function updateDeviceTags(obj) {
  return request({
    url: api.updateDeviceTags,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 新增设备标签

export function createDeviceTags(obj) {
  return request({
    url: api.createDeviceTags,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 删除设备标签

export function deleteDeviceTagType(id) {
  return request({
    url: api.deleteDeviceTagType,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      id: id
    }
  });
} // 获取全部设备标签（设备维护） 设备标签

export function GetDeviceTags() {
  return request({
    url: api.GetDeviceTags,
    method: 'get'
  });
}