import echarts from 'echarts';
export default {
  data: function data() {
    return {
      charts: {
        unit: 'm³/h',
        names: [],
        lineX: [],
        value: [[], []]
      },
      myChart: null,
      timer: null,
      cmd: null
    };
  },
  props: ['data', 'curveData'],
  watch: {
    curveData: function curveData() {
      this.getData();
    }
  },
  methods: {
    getData: function getData() {
      var _this = this;

      var length = this.data[0].fields.length;
      var flow = []; // 有几个流量

      var arr = []; // 最后数值存放的数组

      for (var i = 0; i < length; i++) {
        flow.push("flow".concat(i));
      }

      flow.map(function (i) {
        arr.push({
          name: i,
          value: _this.curveData.map(function (item) {
            return Number(item.data[i]).toFixed(2);
          })
        });
      });
      var X = this.curveData.map(function (item) {
        return _this.$moment.utc(item.time).local().format('YYYY-MM-DD HH:mm:ss');
      });
      this.$set(this.charts, 'lineX', X.reverse());
      arr.map(function (item, index) {
        _this.$set(_this.charts.value, index, item.value.reverse());
      });
      this.init();
    },
    init: function init() {
      var color = ['#17FFF3', '#FF6461', '#FFE4C4'];
      var lineY = [];

      for (var i = 0; i < this.data[0].fields.length; i++) {
        var x = i;

        if (x > color.length - 1) {
          x = color.length - 1;
        }

        var data = {
          name: this.data[0].fields[i].fieldName,
          type: 'line',
          color: color[x],
          smooth: true,
          symbol: 'circle',
          symbolSize: 5,
          data: this.charts.value[i],
          markLine: {
            //最大值和最小值 y轴的标注点  有几条线就会生成几个点, 一样会重叠
            symbol: 'none',
            // 去掉箭头
            data: [{
              name: '上限值',
              yAxis: this.data[0].fields[i].maxNumber,
              label: {
                show: 'true',
                // position: 'left',
                fontSize: 12,
                formatter: '上限' // formatter: '上限 - ' + this.data[0].fields[i].fieldName

              },
              lineStyle: {
                normal: {
                  width: 1,
                  color: color[x]
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#000',
                  fontSize: 0
                }
              },
              symbol: 'circle'
            }, {
              name: '下限值',
              yAxis: this.data[0].fields[i].minNumber,
              label: {
                show: 'true',
                // position: 'left',
                fontSize: 12,
                formatter: '下限' // formatter: '下限 - ' + this.data[0].fields[i].fieldName

              },
              lineStyle: {
                normal: {
                  width: 1,
                  color: color[x]
                }
              },
              symbol: 'circle'
            }]
          }
        };
        lineY.push(data);
      }

      var namesArr = [];
      this.data[0].fields.map(function (item) {
        namesArr.push(item.fieldName);
      });
      var option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: namesArr,
          textStyle: {
            fontSize: 22,
            color: '#00FDFF'
          },
          right: '4%'
        },
        grid: {
          top: '14%',
          left: '4%',
          right: '4%',
          bottom: '12%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.charts.lineX,
          axisLabel: {
            textStyle: {
              color: '#00FDFF'
            },
            formatter: function formatter(params) {
              return params.split(' ')[0] + '\n' + params.split(' ')[1];
            }
          }
        },
        yAxis: {
          name: this.charts.unit,
          type: 'value',
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              color: '#00FDFF'
            }
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#00FDFF'
            }
          }
        },
        series: lineY
      };

      if (!this.myChart) {
        this.myChart = echarts.init(this.$refs.echarts);
      }

      this.myChart.setOption(option);
    }
  }
};