import { GetStatusById } from '@/api/equipmentManagement/status';
import echarts from 'echarts';
export default {
  data: function data() {
    return {
      one: parseInt('00000000', 2),
      two: parseInt('00010000', 2),
      three: parseInt('00100000', 2),
      four: parseInt('00110000', 2),
      allTemp: parseInt('11110000', 2),
      Audio_Rate: null,
      Audio_Sample_OutMode: null,
      floatNumber: '',
      value: [],
      myChart: null,
      timer: null
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.id = this.$route.query.id;
    var dom = this.$refs.sound;
    this.myChart = echarts.init(dom);
    this.getData();
    this.timer = setInterval(function () {
      _this.getData();
    }, 10000);
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    getData: function getData() {
      var _this2 = this;

      GetStatusById(this.id).then(function (res) {
        _this2.Audio_Rate = res.data.statusData['Audio_Rate'];
        _this2.Audio_Sample_OutMode = res.data.statusData['Audio_Sample_OutMode']; // 计算样点数

        if ((_this2.Audio_Sample_OutMode & _this2.allTemp) === _this2.one) {
          _this2.floatNumber = 256;
        } else if ((_this2.Audio_Sample_OutMode & _this2.allTemp) === _this2.two) {
          _this2.floatNumber = 512;
        } else if ((_this2.Audio_Sample_OutMode & _this2.allTemp) === _this2.three) {
          _this2.floatNumber = 1024;
        } else if ((_this2.Audio_Sample_OutMode & _this2.allTemp) === _this2.four) {
          _this2.floatNumber = 4096;
        }

        var value = (res.data.pointData['Audio_Amp'] || []).map(function (item) {
          return [Math.floor(item.key * (_this2.Audio_Rate / _this2.floatNumber)), item.value.toFixed(2)];
        });

        _this2.$set(_this2, 'value', value);

        _this2.init();
      });
    },
    init: function init() {
      var option = {
        tooltip: {
          trigger: 'axis',
          formatter: function formatter(params) {
            return "".concat(params[0].value[0], "<br />\u58F0\u97F3\u9891\u8C31:").concat(params[0].value[1]);
          }
        },
        title: {
          text: '声音频谱图',
          textStyle: {
            color: '#fff',
            fontSize: 26
          },
          subtextStyle: {
            color: '#fff',
            fontSize: 14
          },
          left: 'center',
          top: '2%'
        },
        grid: {},
        xAxis: {
          type: 'value',
          splitLine: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          },
          axisTick: {
            show: true
          },
          axisLabel: {
            color: '#fff',
            fontSize: 14
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            show: false
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#fff'
            }
          },
          axisTick: {
            show: true
          },
          axisLabel: {
            color: '#fff',
            fontSize: 14
          }
        },
        series: [{
          type: 'bar',
          animation: false,
          barWidth: 4,
          data: this.value,
          tooltip: {
            show: false
          },
          itemStyle: {
            color: '#ff8b5d'
          }
        }, {
          type: 'scatter',
          data: this.value,
          symbolSize: 8,
          itemStyle: {
            borderWidth: 0,
            opacity: 1,
            color: '#ff8b5d'
          }
        }]
      };
      this.myChart.setOption(option);
    }
  }
};