import { GetTemplateInfoByDeviceId } from '@/api/equipmentData/dataView'; // 模板接口

import { getDeviceData } from '@/api/devicelist';
import echarts from 'echarts';
export default {
  props: ['data'],
  data: function data() {
    return {
      charts: {
        unit: '℃',
        names: ['A相温度', 'B相温度', 'C相温度', '电机前承轴温度', '电机后承轴温度', '水泵前轴温度', '水泵后轴温度'],
        lineX: [],
        value: [[], [], [], [], [], []]
      },
      myChart: null,
      timer: null,
      cmd: null
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.timer = setInterval(function () {
      _this.getData();
    }, 10000);
    this.getData();
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    getData: function getData() {
      var _this2 = this;

      GetTemplateInfoByDeviceId(this.$route.query.id).then(function (res) {
        res.data.forEach(function (item) {
          if (item.name === '电机温度曲线') {
            _this2.cmd = item.template.cmd;
          }
        });
        getDeviceData({
          cmd: _this2.cmd,
          deviceId: _this2.$route.query.id,
          startTime: _this2.$moment().subtract(30, 'minutes').utc().format(),
          pageIndex: 1,
          pageSize: 200,
          fields: 'data.Temperature0,data.Temperature1,data.Temperature2,data.Temperature3,data.Temperature4,data.Temperature5,data.Temperature6'
        }).then(function (res) {
          // eslint-disable-next-line eqeqeq
          var Temperature0 = res.data.items.map(function (item) {
            return item.data.Temperature0 == 255 ? undefined : item.data.Temperature0.toFixed(2);
          }); // eslint-disable-next-line eqeqeq

          var Temperature1 = res.data.items.map(function (item) {
            return item.data.Temperature1 == 255 ? undefined : item.data.Temperature1.toFixed(2);
          }); // eslint-disable-next-line eqeqeq

          var Temperature2 = res.data.items.map(function (item) {
            return item.data.Temperature2 == 255 ? undefined : item.data.Temperature2.toFixed(2);
          }); // eslint-disable-next-line eqeqeq

          var Temperature3 = res.data.items.map(function (item) {
            return item.data.Temperature3 == 255 ? undefined : item.data.Temperature3.toFixed(2);
          }); // eslint-disable-next-line eqeqeq

          var Temperature4 = res.data.items.map(function (item) {
            return item.data.Temperature4 == 255 ? undefined : item.data.Temperature4.toFixed(2);
          }); // eslint-disable-next-line eqeqeq

          var Temperature5 = res.data.items.map(function (item) {
            return item.data.Temperature5 == 255 ? undefined : item.data.Temperature5.toFixed(2);
          }); // eslint-disable-next-line no-unused-vars

          var Temperature6 = res.data.items.map(function (item) {
            return item.data.Temperature6 == 255 ? undefined : item.data.Temperature6.toFixed(2);
          });
          var X = res.data.items.map(function (item) {
            return _this2.$moment.utc(item.time).local().format('YYYY-MM-DD HH:mm:ss');
          });

          _this2.$set(_this2.charts.value, '0', Temperature0.reverse());

          _this2.$set(_this2.charts.value, '1', Temperature1.reverse());

          _this2.$set(_this2.charts.value, '2', Temperature2.reverse());

          _this2.$set(_this2.charts.value, '3', Temperature3.reverse());

          _this2.$set(_this2.charts.value, '4', Temperature4.reverse());

          _this2.$set(_this2.charts.value, '5', Temperature5.reverse());

          _this2.$set(_this2.charts.value, '6', Temperature6.reverse());

          _this2.$set(_this2.charts, 'lineX', X.reverse());

          _this2.init();
        });
      });
    },
    init: function init() {
      var color = ['#17FFF3', '#FF6461', '#4FAD32', '#A52A2A', '#FFFFC8', '#FFFF82', '#C52A7C'];
      var lineY = [];

      for (var i = 0; i < this.charts.names.length; i++) {
        var x = i;

        if (x > color.length - 1) {
          x = color.length - 1;
        }

        var data = {
          name: this.charts.names[i],
          type: 'line',
          color: color[x],
          smooth: true,
          symbol: 'circle',
          symbolSize: 5,
          data: this.charts.value[i]
        };
        lineY.push(data);
      }

      lineY[0].markLine = {
        silent: true,
        data: [{
          yAxis: 5
        }, {
          yAxis: 100
        }, {
          yAxis: 200
        }, {
          yAxis: 300
        }, {
          yAxis: 400
        }]
      };
      var option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: this.charts.names,
          textStyle: {
            fontSize: 12,
            color: '#00FDFF'
          },
          right: '4%'
        },
        grid: {
          top: '14%',
          left: '4%',
          right: '4%',
          bottom: '12%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.charts.lineX,
          axisLabel: {
            textStyle: {
              color: '#00FDFF'
            },
            formatter: function formatter(params) {
              return params.split(' ')[0] + '\n' + params.split(' ')[1];
            }
          }
        },
        yAxis: {
          name: this.charts.unit,
          type: 'value',
          axisLabel: {
            formatter: '{value}',
            textStyle: {
              color: '#00FDFF'
            }
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#00FDFF'
            }
          }
        },
        series: lineY
      };

      if (!this.myChart) {
        this.myChart = echarts.init(this.$refs.echarts);
      }

      this.myChart.setOption(option);
    }
  }
};