import echarts from 'echarts';
import { withBorder, resize } from '@/mixin';
export default {
  name: 'BoardPanel',
  mixins: [withBorder, resize],
  props: {
    value: {
      default: 90
    },
    title: {
      default: "健康评分"
    },
    symbol: {
      default: '分'
    }
  },
  data: function data() {
    return {
      data: []
    };
  },
  created: function created() {
    this.data = [this.value, this.value, this.value, this.value];
  },
  mounted: function mounted() {
    this.setEchart();
  },
  methods: {
    setEchart: function setEchart() {
      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        backgroundColor: '#fff',
        graphic: [{
          type: 'group',
          left: 'center',
          bottom: 10,
          children: [{
            type: 'text',
            z: 100,
            left: '10',
            top: 'middle',
            style: {
              fill: '#000',
              text: '磁盘剩余空间：',
              font: '16px Microsoft YaHei'
            }
          }, {
            type: 'text',
            z: 100,
            left: '120',
            top: 'middle',
            style: {
              fill: '#000',
              text: '128G',
              font: '24px Microsoft YaHei'
            }
          }]
        }],
        series: [{
          type: 'liquidFill',
          radius: '70%',
          center: ['50%', '40%'],
          data: this.data,
          backgroundStyle: {
            borderWidth: 5,
            borderColor: '#1daaeb',
            color: '#fff'
          },
          label: {
            normal: {
              formatter: (this.value * 100).toFixed(2) + '%',
              textStyle: {
                fontSize: 20
              }
            }
          }
        }]
      };
      this.myChart.setOption(option);
    }
  }
};