import request from '@/utils/request';
var api = {
  UpdateByCode: '/api/company/Config/UpdateByCode',
  GetDeviceLive: '/api/dms/DeviceExtendProperty/GetDeviceLive',
  GetValueByCode: '/api/company/Config/GetValueByCode'
}; // 根据Code修改业务参数

export function UpdateByCode(obj) {
  return request({
    url: api.UpdateByCode,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 获取设备直播扩展属性

export function GetDeviceLive(deviceId) {
  return request({
    url: api.GetDeviceLive,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: deviceId
  });
} // 根据Code获取值

export function GetValueByCode(code) {
  return request({
    url: api.GetValueByCode,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      code: code
    }
  });
}