import request from '@/utils/request';
export default {
  data: function data() {
    return {
      flag: true,
      fileList: [],
      textarea: '',
      visible: false,
      dialogImageUrl: '',
      dialogVisible: false,
      fileData: null,
      form: {
        text: ''
      },
      rules: {
        text: [{
          required: true,
          message: '请输入问题描述',
          trigger: 'blur'
        }]
      }
    };
  },
  methods: {
    uploadFile: function uploadFile(file) {
      var isIMAGE = file.file.type === 'image/jpeg' || file.file.type === 'image/png';
      var isLt1M = file.file.size / 1024 / 1024 < 1;

      if (!isIMAGE) {
        this.$message.error('上传文件只能是图片格式!');
      }

      if (!isLt1M) {
        this.$message.error('上传文件大小不能超过 1MB!');
      }

      if (isIMAGE && isLt1M) {
        this.fileData.append('files', file.file);
      }

      this.flag = isIMAGE && isLt1M;
    },
    submit: function submit() {
      var _this = this;

      this.$refs.form.validate(function (valid) {
        if (valid) {
          _this.flag = true;

          _this.$set(_this, 'fileData', new FormData());

          _this.$refs.upload.submit();

          if (_this.flag) {
            _this.fileData.append('text', _this.form.text);

            request({
              url: '/api/dms/UserFeedback/Create',
              method: 'post',
              headers: {
                'Content-Type': 'application/json'
              },
              data: _this.fileData
            }).then(function () {
              _this.$message({
                type: "success",
                message: "提交成功",
                duration: 500,
                onClose: function onClose() {
                  _this.visible = false;
                }
              });
            });
          }
        }
      });
    },
    init: function init() {
      this.visible = true;
    },
    handlePictureCardPreview: function handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    onClose: function onClose() {
      this.$emit('hidden');
    }
  }
};