import request from '@/utils/request';
var api = {
  login: '/api/User/DMSLogin',
  getInfo: '/api/User/DMSWhoami',
  fristChangePwd: '/api/User/InitPassword',
  changePassword: '/api/User/UpdatePasswordByOld',
  logout: '/api/User/logout',
  sendForpwsSMS: '/api/User/SendForgetPasswordSMS',
  updatePswBySMS: '/api/User/UpdatePasswordBySMS'
};
/**
 * @description 登录
 * @param  {String} {mobile 手机名
 * @param  {String} password}
 */

export function login(obj) {
  return request({
    url: api.login,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      mobile: obj.cellPhone,
      password: obj.password,
      companyCode: obj.organize
    }
  });
}
/**
 * @description 获取当前信息
 * @param  {String} token
 */

export function getInfo() {
  return request({
    url: api.getInfo,
    method: 'get'
  });
}
/**
 * @description 首次登录修改密码(初始化密码)
 * @param  {String} password
 */

export function fristChangePwd(password) {
  return request({
    url: api.fristChangePwd,
    method: 'post',
    headers: {
      'Content-type': 'application/json'
    },
    data: {
      password: password
    }
  });
}
/**
 * @description 修改密码
 * @param  {String} oldPassword 初始密码
 * @param  {String} newPassword 新密码
 */

export function changePassword(oldPassword, newPassword) {
  return request({
    url: api.changePassword,
    method: 'post',
    headers: {
      'Content-type': 'application/json'
    },
    data: {
      oldPassword: oldPassword,
      newPassword: newPassword
    }
  });
}
/**
 * @description 退出登录
 */

export function logout() {
  return request({
    url: api.logout,
    method: 'get'
  });
}
/**
 * @description 发送忘记密码短信验证码
 * @param  {String} mobile 手机号
 * @param  {String} imageCode 图片验证码
 */

export function sendForpwsSMS(mobile, imageCode) {
  return request({
    url: api.sendForpwsSMS,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      mobile: mobile,
      imageCode: imageCode
    }
  });
}
/**
 * @description  根据短信验证码修改密码(重置密码保存)
 * @param  {String} mobile 手机号
 * @param  {String} sms 短信验证码
 * @param  {String} password 密码
 */

export function updatePswBySMS(obj) {
  return request({
    url: api.updatePswBySMS,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}