import router from "./router";
import store from "./store";
import NProgress from 'nprogress'; // progress bar

import 'nprogress/nprogress.css'; // progress bar style

import { getToken } from '@/utils/auth'; // getToken from cookie

NProgress.configure({
  showSpinner: false
}); // NProgress configuration

var whiteList = ['/login']; // 不重定向白名单

router.beforeEach(function (to, from, next) {
  NProgress.start();

  if (getToken()) {
    if (to.path === '/login') {
      next({
        path: '/' + store.state.user.config.DMSHomeRoute
      });
      NProgress.done(); // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else {
      if (store.state.user.getmenu) {
        next();
      } else {
        store.dispatch('GetInfo').then(function (res) {
          // 拉取用户信息
          if (res.success) {
            next({
              path: '/' + store.state.user.config.DMSHomeRoute
            });
          } else {
            store.dispatch('FedLogOut').then(function () {
              // Message.error('Verification failed, please login again')
              next({
                path: '/login'
              });
            });
          }
        }).catch(function () {
          store.dispatch('FedLogOut').then(function () {
            // Message.error(err || 'Verification failed, please login again')
            next({
              path: '/login'
            });
          });
        });
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next();
    } else {
      next("/login?redirect=".concat(to.path)); // 否则全部重定向到登录页

      NProgress.done();
    }
  }
});
router.afterEach(function () {
  NProgress.done(); // 结束Progress
});