import { withBorder, resize } from '@/mixin';
import { GetStatusById } from '@/api/equipmentManagement/status'; // 数据接口

import _ from 'lodash';
import echarts from 'echarts'; // import ecStat from 'echarts-stat'

export default {
  name: 'TurnOnRate',
  mixins: [withBorder, resize],
  props: ['tzqx'],
  data: function data() {
    return {
      legendData: [],
      points: [],
      timer: null
    };
  },
  watch: {
    points: function points() {
      echarts.dispose(this.myChart);
      this.setEchart();
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.setEchart();
    this.getData();
    this.timer = setInterval(function () {
      _this.getData();
    }, 60000);
  },
  methods: {
    setEchart: function setEchart() {
      var _this2 = this;

      JSON.parse(this.tzqx.propertyValue).map(function (item) {
        _this2.legendData.push(item.name);
      });
      var hqRegression = this.regressionFormatter(JSON.parse(this.tzqx.propertyValue)[0].data);
      var paqRegression = this.regressionFormatter(JSON.parse(this.tzqx.propertyValue)[1].data);
      var effqRegression = this.regressionFormatter(JSON.parse(this.tzqx.propertyValue)[2].data);
      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        title: {
          text: '特征曲线',
          left: 'center',
          top: 10
        },
        tooltip: {
          axisPointer: {
            snap: false,
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        toolbox: {
          feature: {
            dataView: {
              show: false,
              readOnly: false
            },
            // magicType: { show: true, type: ['line', 'bar'] },
            restore: {
              show: true
            },
            saveAsImage: {
              show: true
            }
          },
          right: 80
        },
        // color: ['black', 'blue', 'red', '#476fd4'],
        grid: [{
          x: '7%',
          y: '15%',
          height: '30%',
          width: '70%',
          left: '15%'
        }, {
          x: '7%',
          y: '55%',
          height: '30%',
          width: '70%',
          left: '15%'
        }, {
          x: '7%',
          y: '15%',
          height: '70%',
          width: '70%',
          left: '15%'
        } //   { x: '7%', y: '30%', height: '40%', width: '60%', left: '15%' }
        ],
        legend: {
          // data: ['扬程', '轴功率', '效率', '气蚀余量']
          data: this.legendData,
          top: 50
        },
        xAxis: [{
          min: 0,
          splitNumber: 10,
          show: false,
          type: 'value',
          gridIndex: 0
        }, {
          name: 'Q(l/s)',
          nameTextStyle: {
            lineHeight: 1000
          },
          min: 0,
          splitNumber: 10,
          type: 'value',
          gridIndex: 1,
          splitLine: {
            show: false
          }
        }, {
          show: false,
          min: 0,
          splitNumber: 10,
          type: 'value',
          gridIndex: 2
        }, {
          min: 0,
          scale: false,
          splitNumber: 10,
          show: false,
          type: 'value',
          gridIndex: 3
        }],
        yAxis: [{
          name: 'H(m)',
          type: 'value',
          min: 10,
          max: 50,
          gridIndex: 0,
          splitNumber: 5,
          splitLine: {
            show: false
          },
          position: 'left',
          axisLabel: {
            formatter: '{value}' //  H(m)

          },
          z: 5
        }, {
          name: 'Pa(kW)             ',
          type: 'value',
          gridIndex: 1,
          min: function min(value) {
            return Math.floor(value.min / 10) * 10;
          },
          max: function max(value) {
            return Math.ceil(value.max / 10) * 10;
          },
          splitNumber: 6,
          splitLine: {
            show: false
          },
          position: 'left',
          axisLabel: {
            formatter: '{value}'
          },
          z: 5
        }, {
          show: false,
          name: 'Eff(%)',
          type: 'value',
          gridIndex: 2,
          min: -10,
          max: 100,
          splitNumber: 12,
          position: 'left',
          z: 0,
          axisPointer: {
            show: false
          }
        }, {
          name: 'Eff(%)',
          type: 'value',
          gridIndex: 2,
          min: -10,
          onZero: false,
          max: 100,
          splitNumber: 12,
          position: 'right',
          splitLine: {
            show: false
          },
          axisLabel: {
            formatter: function formatter(value) {
              if (value == -10) {
                return '';
              } else {
                return value;
              }
            }
          },
          z: 4
        }, {
          onZero: false,
          show: false,
          type: 'value',
          gridIndex: 3,
          position: 'right',
          min: 2,
          max: 7,
          offset: 80,
          splitLine: {
            show: false
          },
          axisLabel: {
            formatter: '{value}'
          }
        }],
        series: [// {
        //   name: JSON.parse(this.tzqx.propertyValue)[0].name,
        //   type: "scatter",
        //   xAxisIndex: 0,
        //   yAxisIndex: 0,
        //   data: JSON.parse(this.tzqx.propertyValue)[0].data,
        // },
        {
          name: JSON.parse(this.tzqx.propertyValue)[0].name,
          type: "line",
          smooth: true,
          xAxisIndex: 0,
          yAxisIndex: 0,
          data: hqRegression,
          symbol: "none"
        }, // {
        //   name: JSON.parse(this.tzqx.propertyValue)[1].name,
        //   type: "scatter",
        //   xAxisIndex: 1,
        //   yAxisIndex: 1,
        //   data: JSON.parse(this.tzqx.propertyValue)[1].data,
        // },
        {
          name: JSON.parse(this.tzqx.propertyValue)[1].name,
          type: "line",
          smooth: true,
          xAxisIndex: 1,
          yAxisIndex: 1,
          data: paqRegression,
          symbol: "none"
        }, // {
        //   name: JSON.parse(this.tzqx.propertyValue)[2].name,
        //   type: "scatter",
        //   xAxisIndex: 2,
        //   yAxisIndex: 3,
        //   data: JSON.parse(this.tzqx.propertyValue)[2].data,
        // },
        {
          name: JSON.parse(this.tzqx.propertyValue)[2].name,
          type: "line",
          smooth: true,
          xAxisIndex: 2,
          yAxisIndex: 3,
          data: effqRegression,
          symbol: "none"
        }, {
          type: "scatter",
          xAxisIndex: 0,
          yAxisIndex: 0,
          data: this.points,
          itemStyle: {
            color: 'green'
          },
          tooltip: {
            formatter: function formatter(params) {
              var str = 'Q: ' + params.data[0] + ' l/s' + '<br/>' + 'H: ' + params.data[1] + ' m';
              return str;
            }
          }
        } // {
        //   name: '气蚀余量',
        //   type: "line",
        //   xAxisIndex: 3,
        //   yAxisIndex: 3,
        //   data: [,,,,,,,,,]
        // }
        ]
      };
      this.myChart.setOption(option);
    },
    getData: function getData() {
      var _this3 = this;

      GetStatusById(this.$route.query.id).then(function (res) {
        if (res.data.statusData.AmpereA > 5) {
          var h;

          if (res.data.statusData.Pressure0 && res.data.statusData.Pressure1) {
            h = Math.floor((res.data.statusData.Pressure0 * 50 + res.data.statusData.Pressure1 * 50) * 100) / 100;
          } else if (res.data.statusData.Pressure0) {
            h = Math.floor(res.data.statusData.Pressure0 * 10000) / 100;
          } else if (res.data.statusData.Pressure1) {
            h = Math.floor(res.data.statusData.Pressure1 * 10000) / 100;
          }

          var q;

          if (res.data.statusData.flow0 && res.data.statusData.flow1) {
            q = Math.floor((res.data.statusData.flow0 + res.data.statusData.flow1) / 7.2 * 100) / 100;
          } else if (res.data.statusData.flow0) {
            q = Math.floor(res.data.statusData.flow0 / 3.6 * 100) / 100;
          } else if (res.data.statusData.flow1) {
            q = Math.floor(res.data.statusData.flow1 / 3.6 * 100) / 100;
          }

          if (!_this3.points.some(function (item) {
            return _.intersection(item, [q, h]).length === 2;
          })) {
            _this3.points.push([q, h]);
          }
        } // console.log([q,h],)
        // console.log(res.data.statusData.Pressure0,res.data.statusData.Pressure1,'压力')
        // console.log(res.data.statusData.flow0,res.data.statusData.flow1,'流量')

      });
    },
    regressionFormatter: function regressionFormatter(data) {
      // console.log(data,'data')
      // let result = ecStat.regression('polynomial', data);
      // result.points.sort(function(a, b) {
      //   return a[0] - b[0];
      // });
      // console.log(result,'result')
      // return result.points
      return data;
    }
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  }
};