import request from '@/utils/request';
var api = {
  getAllGroup: '/api/dms/Group/GetAll',
  GetDevicesByGroupId: '/api/dms/device/GetDevicesByGroupId',
  GroupGetById: '/api/dms/Group/GetById',
  GetGroupTypes: '/api/dms/Group/GetGroupTypes'
}; // 获取全部分组信息

export function getAllGroup() {
  return request({
    url: api.getAllGroup,
    method: 'get'
  });
} // // 根据分组Id获取分组下的设备

export function GetDevicesByGroupId(obj) {
  return request({
    url: api.GetDevicesByGroupId,
    method: 'get',
    params: obj
  });
} // // 根据ID获取分组信息

export function GroupGetById(id) {
  return request({
    url: api.GroupGetById,
    method: 'get',
    params: {
      id: id
    }
  });
} // // 获得分类分组信息

export function GetGroupTypes() {
  return request({
    url: api.GetGroupTypes,
    method: 'get'
  });
}