import { GetStatusById } from '@/api/equipmentManagement/status'; // 数据接口

export default {
  props: ['codeList', 'position'],
  data: function data() {
    return {
      refresh: true,
      setPosition: [],
      options: [],
      tubesOptions: [[3 / 10, 1 / 10], [5 / 10, 1 / 10], [7 / 10, 1 / 10], [1 / 10, 3 / 10], [3 / 10, 3 / 10], [5 / 10, 3 / 10], [7 / 10, 3 / 10], [9 / 10, 3 / 10], [1 / 10, 5 / 10], [3 / 10, 5 / 10], [7 / 10, 5 / 10], [9 / 10, 5 / 10], [1 / 10, 7 / 10], [3 / 10, 7 / 10], [5 / 10, 7 / 10], [7 / 10, 7 / 10], [9 / 10, 7 / 10], [3 / 10, 9 / 10], [5 / 10, 9 / 10], [7 / 10, 9 / 10]],
      data: {}
    };
  },
  watch: {
    data: function data() {
      var _this = this;

      this.refresh = false;
      this.refresh = true;
      this.setPosition = [];
      this.options = [];
      this.$nextTick(function () {
        _this.init();
      });
    }
  },
  mounted: function mounted() {
    var _this2 = this;

    this.timer = setInterval(function () {
      GetStatusById(_this2.$route.query.id).then(function (res) {
        _this2.$set(_this2, 'data', res.data);
      });
    }, 7000);
    GetStatusById(this.$route.query.id).then(function (res) {
      _this2.$set(_this2, 'data', res.data);
    });
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    init: function init() {
      this.getOptions();
      var canvas = this.$refs.canvas;
      canvas.width = 2400;
      canvas.height = 1000;
      var ctx = canvas.getContext('2d');

      for (var i = 0, len = this.options.length; i < len; i++) {
        this.draw(ctx, this.options[i], i);
      }
    },
    draw: function draw(ctx, obj, index) {
      obj.len = obj.len || 180;
      obj.fz = obj.fz || 80; // 4个角

      ctx.beginPath();
      ctx.moveTo(obj.x + 1 / 4 * obj.len, obj.y);
      ctx.lineTo(obj.x + 3 / 4 * obj.len, obj.y);
      ctx.lineTo(obj.x + obj.len, obj.y + 1 / 4 * obj.len);
      ctx.lineTo(obj.x + obj.len, obj.y + 3 / 4 * obj.len);
      ctx.lineTo(obj.x + 3 / 4 * obj.len, obj.y + obj.len);
      ctx.lineTo(obj.x + 1 / 4 * obj.len, obj.y + obj.len);
      ctx.lineTo(obj.x, obj.y + 3 / 4 * obj.len);
      ctx.lineTo(obj.x, obj.y + 1 / 4 * obj.len);
      ctx.closePath();
      ctx.fillStyle = '#ccc';
      ctx.fill();

      if (obj.text === 1 && this.data.pointData['Front.Groove.X' + this.codeList[index] + '.Pallet.Tubes.Status']) {
        for (var i = 0; i < this.data.pointData['Front.Groove.X' + this.codeList[index] + '.Pallet.Tubes.Status'].length; i++) {
          ctx.save();
          ctx.beginPath();
          ctx.arc(obj.x + this.tubesOptions[i][0] * obj.len, obj.y + this.tubesOptions[i][1] * obj.len, obj.len * 1 / 12, 0, 360, false);

          if (this.data.pointData['Front.Groove.X' + this.codeList[index] + '.Pallet.Tubes.Status'][i].value) {
            ctx.fillStyle = 'purple';
            ctx.fill();
            ctx.closePath();
            ctx.restore();
          } else {
            ctx.lineWidth = 2;
            ctx.stroke();
            ctx.fillStyle = '#909399';
            ctx.fill();
            ctx.closePath();
            ctx.restore();
          }
        }

        if (this.data.pointData['Front.Groove.X' + this.codeList[index] + '.Pallet.Tubes.Status'] && this.data.pointData['Front.Groove.X' + this.codeList[index] + '.Pallet.Tubes.Status'].length > 0) {
          ctx.beginPath();
          ctx.arc(obj.x + 1 / 2 * obj.len, obj.y + 1 / 2 * obj.len, obj.len * 1 / 10, 0, 360, false);
          ctx.fillStyle = 'black';
          ctx.fill();
          ctx.closePath();
        }
      }
    },
    getOptions: function getOptions() {
      var _this3 = this;

      // 判断是否自己传入排列顺序
      if (this.position) {
        this.setPosition = this.position;
      } else {
        for (var i = 0; i < 12; i++) {
          // 添加3排 12列
          this.setPosition.push([i * 200, 200]);
          this.setPosition.push([i * 200, 200 + 1 * 200]);
          this.setPosition.push([i * 200, 200 + 2 * 200]);
        } // this.setPosition.splice(this.setPosition.length - 1, 1)
        // 最上面有几个不规律的单独添加


        this.setPosition.splice(9, 0, [700, 0]);
        this.setPosition.splice(13, 0, [900, 0]);
        this.setPosition.splice(20, 0, [1300, 0]);
        this.setPosition.splice(24, 0, [1500, 0]);
      }

      this.codeList = this.codeList || [];
      this.options = this.setPosition.map(function (item, index) {
        return {
          x: item[0],
          y: item[1],
          text: typeof _this3.data.statusData['Front.Groove.X' + _this3.codeList[index] + '.Status'] === 'undefined' ? '' : _this3.data.statusData['Front.Groove.X' + _this3.codeList[index] + '.Status']
        };
      });
    }
  }
};