import echarts from 'echarts';
export default {
  name: 'Spectrometer',
  props: ['data', 'prefix'],
  data: function data() {
    return {
      optionDN: {},
      optionREF: {},
      DNX: [],
      DNY: [],
      refX: [],
      refY: []
    };
  },
  watch: {
    data: function data() {
      this.DNX = [];
      this.DNY = [];
      this.refX = [];
      this.refY = [];
      this.achieveState(this.prefix);
    }
  },
  mounted: function mounted() {
    this.achieveState(this.prefix);
  },
  methods: {
    achieveState: function achieveState(prefix) {
      var _this = this;

      if (!prefix) {
        prefix = '';
      } // 获取数据


      new Promise(function (resolve) {
        if (_this.data) {
          if (!_this.data.pointData) {
            return;
          } // 如果里面没有值就return


          _this.data.pointData.DN.map(function (item) {
            _this.DNX.push(item.key);

            _this.DNY.push(item.value);
          });

          _this.data.pointData.Reflectivity.map(function (item) {
            _this.refX.push(item.key);

            _this.refY.push(item.value);
          });

          resolve();
        }
      });
      this.echartShow();
      this.refAmpShow();
    },
    echartShow: function echartShow() {
      // DN值 echart
      var container = this.$refs.dnValue;
      var myChart = echarts.init(container);
      this.optionDN = {
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        animation: false,
        legend: {
          data: ['DN值']
        },
        toolbox: {
          show: true
        },
        calculable: true,
        dataZoom: {
          show: true,
          realtime: true,
          start: 0,
          end: 100
        },
        grid: {
          bottom: '70px'
        },
        xAxis: [{
          type: 'category',
          boundaryGap: true,
          data: this.DNX,
          axisLabel: {
            rotate: 40
          },
          axisLine: {
            lineStyle: {
              color: '#000',
              // 颜色
              width: 6 // 粗细

            }
          },
          minInterval: 1
        }],
        yAxis: [{
          type: 'value',
          data: this.DNY,
          axisLabel: {
            formatter: function formatter(value) {
              return value;
            }
          }
        }],
        series: [{
          name: 'DN值',
          type: 'line',
          showAllSymbol: true,
          // 标注所有数据点,
          symbol: 'circle',
          // 拐点样式
          symbolSize: 4,
          // 拐点大小
          data: this.DNY,
          itemStyle: {
            normal: {
              borderColor: '#000',
              color: '#000',
              lineStyle: {
                type: 'solid',
                width: 1
              }
            }
          }
        }]
      };
      myChart.setOption(this.optionDN);
    },
    refAmpShow: function refAmpShow() {
      // 反射率echart
      var container = this.$refs.reflectivity;
      var myChart = echarts.init(container);
      this.optionREF = {
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        animation: false,
        legend: {
          data: ['反射率']
        },
        toolbox: {
          show: true
        },
        calculable: true,
        dataZoom: {
          show: true,
          realtime: true,
          start: 0,
          end: 100
        },
        grid: {
          bottom: '70px'
        },
        xAxis: [{
          type: 'category',
          boundaryGap: true,
          data: this.refX,
          axisLabel: {
            rotate: 40
          },
          axisLine: {
            lineStyle: {
              color: '#000',
              // 颜色
              width: 6 // 粗细

            }
          },
          minInterval: 1
        }],
        yAxis: [{
          type: 'value',
          data: this.refY,
          axisLabel: {
            formatter: function formatter(value) {
              return value.toFixed(3);
            }
          }
        }],
        series: [{
          name: '反射率',
          type: 'line',
          showAllSymbol: true,
          // 标注所有数据点,
          symbol: 'circle',
          // 拐点样式
          symbolSize: 4,
          // 拐点大小
          data: this.refY,
          itemStyle: {
            normal: {
              borderColor: '#000',
              lineStyle: {
                type: 'solid',
                width: 1
              },
              color: '#000'
            }
          }
        }]
      };
      myChart.setOption(this.optionREF);
    }
  }
};