import BoardButton from '@/components/boardButton';
export default {
  name: 'Header',
  props: ['activeHome', 'istest'],
  components: {
    BoardButton: BoardButton
  },
  data: function data() {
    return {
      PCSiteName: '',
      DMSLogo: '',
      isFull: false
    };
  },
  created: function created() {
    this.PCSiteName = this.$store.state.user.config.PCSiteName;
    this.DMSLogo = this.$store.state.user.config.DMSLogo;
  },
  mounted: function mounted() {
    var _this = this;

    this.isFull = !!(document.webkitIsFullScreen || document.mozFullScreen || document.msFullscreenElement || document.fullscreenElement);
    window.addEventListener('resize', function () {
      _this.isFull = !!(document.webkitIsFullScreen || document.mozFullScreen || document.msFullscreenElement || document.fullscreenElement);
    });
  },
  methods: {
    handleChangeScreen: function handleChangeScreen() {
      if (this.isFull) {
        this.exitFullscreen();
      } else {
        this.tofullScreen();
      }
    },
    exitFullscreen: function exitFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      }
    },
    tofullScreen: function tofullScreen() {
      var element = document.documentElement;

      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      }
    },
    toDashboard: function toDashboard() {
      if (this.$route.name === 'Board') {
        return;
      } else {
        this.$router.push('/board');
      }
    },
    toHome: function toHome() {
      this.$router.push('/home/home');
    },
    logout: function logout() {
      this.$store.dispatch('LogOut').then(function () {
        location.reload(); // 为了重新实例化vue-router对象 避免bug
      });
    }
  }
};