export default {
  name: 'Status',
  components: {},
  props: ['data'],
  data: function data() {
    return {
      statusData: {},
      connected: '',
      WiFi_Status: '',
      ElectricityConsum: '',
      RunYear: ''
    };
  },
  watch: {
    data: function data() {
      this.getData();
    }
  },
  created: function created() {
    this.getData();
  },
  methods: {
    getData: function getData() {
      var _this = this;

      this.connected = this.data[0].contected;
      this.data[0].fields.map(function (item) {
        if (item.fieldCode == "YearHour") {
          _this.RunYear = Number(item.fieldValue).toFixed(2);
        } else if (item.fieldCode == "WiFi_Status") {
          _this.WiFi_Status = item.fieldValue;
        } else if (item.fieldCode == "ElectricityConsum") {
          _this.ElectricityConsum = Number(item.fieldValue).toFixed(2);
        } else if (item.fieldCode == "RunYear") {
          _this.RunYear = Number(item.fieldValue).toFixed(2);
        }
      });
    }
  },
  mounted: function mounted() {}
};