import echarts from 'echarts';
export default {
  name: 'Diagram',
  props: ['lineData'],
  watch: {
    lineData: function lineData() {
      this.initEchart();
    }
  },
  mounted: function mounted() {
    this.initEchart();
  },
  methods: {
    initEchart: function initEchart() {
      var dom = this.$refs.container;
      var myChart = echarts.init(dom);
      var series = this.lineData.data.map(function (item) {
        return {
          name: item.title,
          type: 'line',
          itemStyle: {
            normal: {
              lineStyle: {
                type: 'solid'
              }
            }
          },
          smooth: true,
          // 曲线平滑
          data: item.data
        };
      });
      var legend = (this.lineData.data || []).map(function (item) {
        return item.title;
      });
      var option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: legend
        },
        toolbox: {
          right: '40',
          top: '16',
          show: true,
          feature: {
            mark: {
              show: true
            },
            saveAsImage: {
              name: this.lineData.name
            }
          }
        },
        calculable: false,
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: this.lineData.xtitle,
          inverse: true
        }],
        yAxis: [{
          type: 'value'
        }],
        series: series
      }; // 使用刚指定的配置项和数据显示图表。

      myChart.setOption(option);
    },
    formatter: function formatter(row) {
      return row.address;
    }
  }
};