import vue from '@/main.js';
export function isvalidUsername(str) {
  var valid_map = ['admin', 'editor'];
  return valid_map.indexOf(str.trim()) >= 0;
}
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path);
}
export var validPhone = function validPhone(rule, value, callback) {
  var reg = /^1[23456789]\d{9}$/;

  if (!value) {
    callback(new Error(vue.$t('validate.telephone')));
  } else if (!reg.test(value)) {
    callback(new Error(vue.$t('validate.validataPhone')));
  } else {
    callback();
  }
};
export var validEmail = function validEmail(rule, value, callback) {
  // eslint-disable-next-line no-useless-escape
  var mailReg = /^([^\\\/\^])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;

  if (!value) {
    return callback(new Error(vue.$t('validate.email')));
  }

  if (mailReg.test(value)) {
    callback();
  } else {
    callback(new Error(vue.$t('validate.validataEmail')));
  }
};