import { withBorder, resize } from '@/mixin';
import echarts from 'echarts';
export default {
  name: 'HalfYearTraffic',
  props: {
    title: {
      default: function _default() {
        return '最近六个月流量统计情况 (m³/h)';
      }
    },
    xData: {
      default: function _default() {
        return ['四月', '五月', '六月', '七月', '八月', '九月'];
      }
    },
    yData: {
      default: function _default() {
        return [14, 12, 15, 13, 13, 14.8];
      }
    }
  },
  mixins: [withBorder, resize],
  computed: {
    allProps: function allProps() {
      return {
        title: this.title,
        xData: this.xData,
        yData: this.yData
      };
    }
  },
  watch: {
    allProps: function allProps() {
      this.setEchart();
    }
  },
  mounted: function mounted() {
    this.setEchart();
  },
  methods: {
    setEchart: function setEchart() {
      var dom = this.$refs.echart;
      this.myChart = echarts.init(dom);
      var option = {
        title: {
          text: this.title,
          top: -5,
          left: 'center',
          textStyle: {
            color: '#d9efff'
          }
        },
        xAxis: [{
          type: 'category',
          color: 'red',
          data: this.xData,
          axisPointer: {
            type: 'line'
          },
          axisLine: {
            lineStyle: {
              color: '#3490ba'
            }
          },
          axisLabel: {
            margin: 20,
            color: '#fff',
            textStyle: {
              fontSize: 12
            }
          }
        }],
        yAxis: [{
          min: 0,
          max: 18,
          axisLabel: {
            color: '#fff'
          },
          axisLine: {
            lineStyle: {
              color: '#3490ba'
            }
          },
          splitLine: {
            lineStyle: {
              color: '#272456'
            }
          }
        }],
        series: [{
          type: 'bar',
          data: this.yData,
          barWidth: '20px',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: '#41E1D4' // 0% 处的颜色

              }, {
                offset: 1,
                color: '#10A7DB' // 100% 处的颜色

              }], false),
              barBorderRadius: [30, 30, 0, 0],
              shadowColor: 'rgba(0,255,225,1)',
              shadowBlur: 4
            }
          },
          label: {
            normal: {
              show: true,
              lineHeight: 30,
              width: 80,
              height: 30,
              backgroundColor: '#252453',
              borderRadius: 200,
              position: ['-8', '-60'],
              distance: 1,
              formatter: ['    {d|●}', ' {a|{c}%}     \n', '    {b|}'].join(','),
              rich: {
                d: {
                  color: '#3CDDCF'
                },
                a: {
                  color: '#fff',
                  align: 'center'
                },
                b: {
                  width: 1,
                  height: 30,
                  borderWidth: 1,
                  borderColor: '#234e6c',
                  align: 'left'
                }
              }
            }
          }
        }]
      };
      this.myChart.setOption(option);
    }
  }
};